<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>系统消息</p>
        </section>
        <section class="middle">
            <md-tab-bar
                    v-model="current"
                    :items="items"
                    :has-ink="false"
                    @change="onTabChange"
            ></md-tab-bar>
            <div class="none" v-if="!listLeft.length"  :style="{height: Height}">暂无数据</div>
            <md-scroll-view v-if="listLeft.length"  :style="{height: Height}" @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                <div class="expert">
                    <div class="list"
                         v-for="i in listLeft"
                         :key="i.id">
                        <div class="img" v-if="i.typeId === 1 && i.readTime">
                            <img v-if="!i.readTime" src="../../../assets/personal/message/333.png" alt="">
                            <img v-if="i.readTime" src="../../../assets/personal/message/334.png" alt="">
                        </div>
                        <div class="img" v-if="i.typeId === 2">
                            <img v-if="!i.readTime" src="../../../assets/personal/message/111.png" alt="">
                            <img v-if="i.readTime" src="../../../assets/personal/message/112.png" alt="">
                        </div>
                        <div class="img" v-if="i.typeId === 3">
                            <img v-if="!i.readTime" src="../../../assets/personal/message/555.png" alt="">
                            <img v-if="i.readTime" src="../../../assets/personal/message/556.png" alt="">
                        </div>
                        <div class="img" v-if="i.typeId === 4">
                            <img v-if="!i.readTime" src="../../../assets/personal/message/222.png" alt="">
                            <img v-if="i.readTime" src="../../../assets/personal/message/223.png" alt="">
                        </div>
                        <div class="img" v-if="i.typeId === 5">
                            <img v-if="!i.readTime" src="../../../assets/personal/message/666.png" alt="">
                            <img v-if="i.readTime" src="../../../assets/personal/message/667.png" alt="">
                        </div>
                        <div class="img" v-if="i.typeId === 6">
                            <img v-if="!i.readTime" src="../../../assets/personal/message/445.png" alt="">
                            <img v-if="i.readTime" src="../../../assets/personal/message/444.png" alt="">
                        </div>
                        <div class="right" @click="showDetail(i.typeId, i.detail)">
                            <div class="title">{{messageTitle[i.typeId]}}</div>
                            <div class="middle1">{{ i.detail }}</div>
                        </div>
                    </div>
                </div>
                <md-scroll-view-more slot="more" :is-finished="isFinished">
                </md-scroll-view-more>
            </md-scroll-view>
            <md-dialog
                    :closable="true"
                    v-model="iconDialog.open"
                    :btns="iconDialog.btns"
            >
                <img v-if="iconDialog.type === 1" class="dialog-img" src="../../../assets/personal/message/3.png" alt="">
                <img v-if="iconDialog.type === 2" class="dialog-img" src="../../../assets/personal/message/6.png" alt="">
                <img v-if="iconDialog.type === 3" class="dialog-img" src="../../../assets/personal/message/5.png" alt="">
                <img v-if="iconDialog.type === 4" class="dialog-img" src="../../../assets/personal/message/2.png" alt="">
                <img v-if="iconDialog.type === 5" class="dialog-img" src="../../../assets/personal/message/4.png" alt="">
                <img v-if="iconDialog.type === 6" class="dialog-img" src="../../../assets/personal/message/1.png" alt="">
                <p style="color: #021955;font-size: 16px">{{ messageTitle[iconDialog.type] }}</p>
                <p style="color: #666666;font-size: 14px">{{ iconDialog.message }}!</p>
            </md-dialog>
        </section>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/personal/message.js'
import { messageTitle } from '@/utils/select.js'
import { TabBar, Swiper, Dialog, Icon, SwiperItem, ScrollView, ScrollViewMore, Toast } from 'mand-mobile'
export default {
  name: 'News',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [Icon.name]: Icon,
    [Dialog.name]: Dialog,
    [ScrollViewMore.name]: ScrollViewMore,
    [Toast.name]: Toast
  },
  data () {
    return {
      // 当前选中tab
      current: 1,
      // tab项
      items: [
        { name: 1, label: '全部消息', current: '' },
        { name: 2, label: '活动通知', current: 1 },
        { name: 3, label: '技术对接', current: 2 },
        { name: 4, label: '发布反馈', current: 3 },
        { name: 5, label: '项目申报', current: 4 },
        { name: 6, label: '平台认证', current: 5 },
        { name: 7, label: '管理员通知', current: 6 }
      ],
      listLeft: [],
      messageTitle: messageTitle,
      isFinished: false,
      Height: '900px',
      listRight: [],
      searchParams: {
        id: 1,
        pageSize: 10,
        pageNumber: 1
      },
      iconDialog: {
        open: false,
        type: '',
        message: ''
      }
    }
  },
  mounted () {
    this.getList()
    this.Height = window.innerHeight - 84 + 'px'
  },
  methods: {
    getList () {
      getList(this.searchParams).then(res => {
        this.listLeft = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    showDetail (id, message) {
      this.iconDialog.open = true
      this.iconDialog.type = id
      this.iconDialog.message = message
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.searchParams.pageNumber = 1
      this.searchParams.typeId = item.current
      this.getList()
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push({
        path: '/news/detail',
        query: { id: i.id }
      })
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.listLeft = this.listLeft.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    // 回退
    goBack () {
      this.$router.push('/personal')
    }
  }
}
</script>

<style lang="stylus" scoped>
    .middle
        position fixed
        top 48px
        width 100%
    /deep/.md-tab-bar-list .is-active
        color #021955!important
        background #FFF!important
        border-bottom 4px solid #021955
    /deep/.md-tab-bar-item
        background #fff!important
        color #999999!important
    .md-example-child-tab-bar-6 .md-swiper
        background #FFF
        .md-swiper-item
            padding 20px
            box-sizing border-box
            font-size 24px
            line-height 1.5
            color #666
    .md-example-child-scroll-view-0
        height 600px
        background #FFF
        .scroll-view-item
            margin-bottom 30px
            text-align center
            font-size 28px
            font-family DINAlternate-Bold
            height 174px
    .dialog-img
        width 330px
        height 300px
        margin-top -188px
    .scroll-view-container
        .scroll-view-item
            margin-bottom  50px
            padding-bottom 20px
            border-bottom 2px solid #666666
            display flex
            justify-content space-between
            img
                width 260px
                height 152px
            .article
                width 410px
                text-align left
                margin 10px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                    text-overflow ellipsis
                    overflow hidden
                    display -webkit-box
                    -webkit-line-clamp 2
                    -webkit-box-orient vertical
                span
                    display flex
                    align-items center
                    line-height 24px
                    justify-content: space-between
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
    /deep/.md-tab-bar .md-tab-bar-list
        border-top 0!important
    .md-scroll-view
        padding 20px
    .expert
        padding 10px 30px
        .list
            height 120px
            .img
                width 60px
                height 60px
                float left
                box-sizing border-box
                img
                    width 60px
                    height 60px
            .right
                width 85%
                padding 20px
                float left
                text-align left
                border-bottom 1px solid #EEEEEE
                .title
                    overflow hidden
                    font-size 28px
                    line-height 40px
                    height 40px
                    color #333333
                .middle1
                    font-size 24px
                    height 40px
                    overflow hidden
                    line-height 40px
                    color #666666
    /deep/.md-popup-box
        overflow visible
    /deep/.md-dialog-content
        overflow visible
</style>
